@import url(https://fonts.googleapis.com/css2?family=Lexend+Peta&display=swap);
body{box-sizing:border-box;margin:0;padding:0;font-family:'Lexend Peta', sans-serif;color:#D1D1D1}body,#root,.App{display:flex;width:100vw;height:100vh}.App{flex-direction:column;align-items:center;justify-content:center;background:#fff;background:radial-gradient(circle, #fff 31%, #ebebeb 100%);filter:progid:DXImageTransform.Microsoft.gradient(startColorstr="#ffffff",endColorstr="#ebebeb",GradientType=1)}.main-timer{display:flex;flex-direction:column;width:340px;border:none;border-radius:65px;box-shadow:0px 0px 20px 4px #858585, 0px 0px 6px 1px #0000007d}.main-timer .head-timer{display:flex;align-items:center;justify-content:space-evenly;height:155px;border:2px rgba(209,209,209,0.545098);border-style:none none solid none;border-radius:65px 65px 0 0;background:#000000}.main-timer .body-timer{display:flex;flex-direction:column;align-items:center;height:390px;border-radius:0px 0px 65px 65px;background:transparent linear-gradient(180deg, #174262 0%, #05111A 100%) 0% 0% no-repeat padding-box}.main-timer .body-timer_break{background:transparent linear-gradient(180deg, #421762 0%, #05111A 100%) 0% 0% no-repeat padding-box}.session-element{display:flex;flex-direction:column;align-items:center;justify-content:flex-end}.session-element h5{margin:0 auto;font-size:15px}#break-label,#session-label{display:flex;align-items:center;justify-content:center;width:112px;font-size:37px}#break-label button,#session-label button{background:#000000;border:none;outline:none}#break-label button:hover,#session-label button:hover{border:none;outline:none;color:#fff}#break-decrement,#break-increment,#session-decrement,#session-increment{cursor:pointer;font-size:27px;color:rgba(209,209,209,0.545098)}#session-length,#break-length{min-width:54px;text-align:center;margin:0 15px}.disabled{cursor:not-allowed !important}.timer{height:280px;display:flex;flex-direction:column;align-items:center;justify-content:center}.timer #time-left{margin:0 auto;min-width:210px;font-size:64px}.timer #timer-label{text-align:center;font-size:19px}.timer #timer-label span{font-size:26px}.controls{display:flex;justify-content:space-between;width:220px;cursor:pointer}#start_stop,#reset{opacity:0.7}#start_stop:hover,#reset:hover{opacity:1}

